<template>
  <div>
    <h3>Todos</h3>
    <div class="legend">
      <span>Double click to mark as complete</span>
      <span> <span class="complete-box"></span>=Complete Box </span>
      <span> <span class="incomplete-box"></span>=Incomplete Box </span>
    </div>
    <div class="todos">
      <div
        @dblclick="onDblClick(todo)"
        v-for="todo in allTodos"
        :key="todo.id"
        class="todo"
        :class="{ 'is-completed': todo.completed }"
      >
        {{ todo.title }}
        <i @click="deleteTodo(todo.id)" class="fas fa-trash-alt"></i>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from "vuex";
  export default {
    name: "ToDos",
    computed: {
      ...mapGetters(["allTodos"]),
    },
    methods: {
      ...mapActions(["fetchTodos", "deleteTodo", "updateTodo"]),
      onDblClick(todo) {
        todo.completed = !todo.completed;
        this.updateTodo(todo);
      },
    },
    created() {
      this.fetchTodos();
    },
  };
</script>

<style scoped>
  .todos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
  }
  .todo {
    border: 1px solid #ccc;
    background: #41b883;
    padding: 1rem;
    border-radius: 5px;
    text-align: center;
    position: relative;
    cursor: pointer;
  }
  .is-completed {
    background: #35495e;
    color: #ccc;
  }
  i {
    position: absolute;
    bottom: 10px;
    right: 10px;
    color: #fff;
    cursor: pointer;
  }
  .legend {
    display: flex;
    justify-content: space-around;
    margin-bottom: 1rem;
  }
  .complete-box {
    display: inline-block;
    width: 15px;
    height: 15px;
    background: #35495e;
  }
  .incomplete-box {
    display: inline-block;
    width: 15px;
    height: 15px;
    background: #41b883;
  }
  @media (max-width: 500px) {
    .todos {
      grid-template-columns: 1fr;
    }
  }
</style>
